.explanation {
  width: @mobile-width;
  margin: 0 auto;
}

.explanation__title {
  margin-top: 5px;
  display: inline-block;

  font-size: 36px;
  font-weight: 300;
}

.explanation__list {
  margin-top: 9px;
}

@media screen and (min-width: @tablet-viewport) {
  .explanation {
    width: @tablet-width;
  }

  .explanation__title {
    margin-top: 10px;
    font-size: @tablet-font * 3;
  }

  .explanation__list {
    margin-top: 18px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

@media screen and (min-width: @desktop-viewport) {
  .explanation {
    width: @desktop-width;
    margin-top: 80px;
  }

  .explanation__title {
    margin-top: -12px;
  }

  .explanation__list {
    margin-top: 18px;
  }
}
